import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  TextField,
  makeStyles,
  CardMedia,
  CardHeader,
  Divider,
  Paper,
  FormHelperText,
  FormControlLabel,
  Checkbox,
  RadioGroup,
  Radio,
  Typography,
  Tooltip
} from '@material-ui/core';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import wait from 'src/utils/wait';



import useIsMountedRef from 'src/hooks/useIsMountedRef';
import { useHistory } from 'react-router-dom';
import {getUserIdToken} from 'src/api/users';

import { H5P_SERVER_URL } from 'src/config';
const useStyles = makeStyles(() => ({
  brief: {
    '& .ql-editor': {
      height: 150
    }
  },
  tags: {
    position: 'relative',
    overflow: 'auto',
    maxHeight: 300
  },
  categories: {
    position: 'relative',
    overflow: 'auto',
    maxHeight: 300
  }
}));

const H5pCreateForm = ({ className, ...rest }) => {
  const history = useHistory();
  const isMountedRef = useIsMountedRef();
  const classes = useStyles();
  let [idToken, setIdToken] = useState();
  const getIdToken = useCallback(async () => {
    try {
      const idToken = await getUserIdToken();
      console.log(idToken);
      if (isMountedRef.current) {
        setIdToken(idToken);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {getIdToken()}, [getIdToken]);

  console.log(idToken)

  return (
    <Formik
      initialValues={{
        title: '',
        url: '',
        submit: null
      }}
      validationSchema={Yup.object().shape({
        title: Yup.string().max(255)
      })}
      onSubmit={async (values, {
        resetForm,
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {
          console.log(values);
          // NOTE: Make API request
          // addLivestreamH5p(values);
          // await wait(500);
          history.push('/teacher/management/h5p');
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          setFieldValue,
          isSubmitting,
          touched,
          values
        }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit}{...rest}>
          <Card>
            <Box minWidth={700}>
              {idToken
              ? <CardMedia
              component="iframe"
              height="700"
              image={H5P_SERVER_URL+"/h5p/new?idToken="+idToken}
              title="New H5p"
            />
              : <TextField
              fullWidth
              label="Tiêu đề"
              name="title"
              value=""
              variant="outlined"
            />
            }

              </Box>
          </Card>
        </form>
      )}
    </Formik>
  );
};

H5pCreateForm.propTypes = {
  className: PropTypes.string
};

export default H5pCreateForm;
